document.querySelector(".JoinGameButton").addEventListener("mousedown", function(){
  var InputKey = document.querySelector(".EnterKey").value;
  console.log(InputKey);
  var Keysarr = [];
  firebaseRef = firebase.database().ref();
  firebaseRef.once("value", function(snapshot) {
      vlala = snapshot.val();
      Keysarr = Object.keys(vlala);
      console.log(Keysarr);
  });
  setTimeout(function(){
    Keysarr.forEach(function(e){
      if(e == InputKey){
        console.log("Key gefunden");
        var Freespace = "";
        firebaseRef = firebase.database().ref(InputKey);
        firebaseRef.child("FreiePl√§tze").on("value", function(snapshot) {
          Freespace = snapshot.val();
        });
        if(Freespace > 0){
          GameKey = InputKey;
          firebaseRef = firebase.database().ref(GameKey);
          firebaseRef.update({
              "FreiePl√§tze": --Freespace,
          });
          mehrAlsZweiSpieler = true;
          document.querySelector(".JoinGame").style.display = "none";
          document.getElementById("wrapperloader3").style.display = "block";
          defaultgamescreen();
          setTimeout(function(){
            document.getElementById("wrapperloader3").style.display = "none";
            document.querySelector(".top-banner-morePlayers").style.display = "flex";
            firebaseRef = firebase.database().ref(GameKey+"/Players");
            firebaseRef.child(username).set(0);
            document.querySelector(".aside").style.display = "inline";
            document.querySelector(".aside2").style.display = "inline";
            //document.querySelector(".containerMonitor").style.display = "inline";
            //document.querySelector(".choose-username").style.display = "flex";
            //updateFlag();
          },5000)
          firebaseRef = firebase.database().ref(GameKey);
          firebaseRef.child("GameStart").on("value", function(snapshot) {
            Gamestart = snapshot.val();
            if(Gamestart == "true"){
                document.getElementById("wrapperloader3").style.display = "none";
                document.querySelector(".demo").style.display = "inline";
                setTimeout(function(){
                    document.querySelector(".demo").style.display = "none";
                    document.querySelector(".containerMonitor").style.display = "inline";
                    document.querySelector(".choose-username").style.display = "flex";
                    updateFlag();
                }, 5000);
            }
          });
          firebaseRef = firebase.database().ref(GameKey+"/Players");
          firebaseRef.on("value", function(snapshot) {
            vlala = snapshot.val();
            Playersarr = Object.keys(vlala);
            console.log(Playersarr);
            aktuell = 0;
            Playersarr.forEach(function(e){
              if(aktuell == 0 && e != username){
                document.getElementById("ENEMY").textContent = e;
                aktuell ++;
                Enemy = e;
              }
              else if(aktuell == 1 && e != username){
                document.getElementById("ENEMY2").textContent = e;
                aktuell ++;
                Enemy2 = e;
              }
              else if(aktuell == 2 && e != username){
                document.getElementById("ENEMY3").textContent = e;
                aktuell ++;
                Enemy3 = e;
              }
            });
            firebaseRef = firebase.database().ref(GameKey);
            document.getElementById("ME").textContent = username;
            firebaseRef.child(username).on("value", function(snapshot) {
              userscore = snapshot.val();
              countUpMe(userscore);
            });
            firebaseRef = firebase.database().ref(GameKey);
            firebaseRef.child(Enemy).on("value", function(snapshot) {
              Enemyscore = snapshot.val();
              countUpEnemy(Enemyscore);
              console.log(Enemyscore);
            });
            firebaseRef = firebase.database().ref(GameKey);
            firebaseRef.child(Enemy2).on("value", function(snapshot) {
              Enemyscore2 = snapshot.val();
              countUpEnemy2(Enemyscore2);
              console.log(Enemyscore2);
            });
            firebaseRef = firebase.database().ref(GameKey);
            firebaseRef.child(Enemy3).on("value", function(snapshot) {
              Enemyscore3 = snapshot.val();
              countUpEnemy3(Enemyscore3);
              console.log(Enemyscore3);
            });
            aktuell = 0;
          });
        }
      }
    });
  },2000);
});

