function matchmaked () {

  firebaseRef = firebase.database().ref("matchmaking");

  firebaseRef.child(username).on("value", function(snapshot){
    updated = snapshot.val();
    console.log(updated);
    if(updated != 1){
      if(updated != null){
        GameKey = updated;
        console.log("Der Gamekey ist:"+GameKey);
        console.log(username);
        document.querySelector(".matchcontainer2").style.display = "none";
        document.querySelector(".matchcontainer").style.display = "none";
        document.querySelector(".Abrechen").style.display = "none";
        document.getElementById("wrapperloader3").style.display = "block";
        defaultgamescreen();
        firebaseRef = firebase.database().ref(GameKey);
        firebaseRef.child("players").on("value", function(snapshot) {
            Players = snapshot.val();
            maybeenemy = Players.split("+", 2);
            if(maybeenemy[0] == username) {
              document.getElementById("ME").textContent = username;
              document.getElementById("ENEMY").textContent = maybeenemy[1];
              Enemy = maybeenemy[1];
              console.log(Enemy + "an zweiter Stelle");
            } else{
                document.getElementById("ME").textContent = username;
                document.getElementById("ENEMY").textContent = maybeenemy[0];
                setTimeout(function(){
                    document.querySelector(".wrapperGameselect").style.display = "block";
                },7000);
                Admin = username;
                Enemy = maybeenemy[0];
                console.log(Enemy + "an erster Stelle");
            }
            firebaseRef = firebase.database().ref(GameKey);
            console.log(Enemy);
            firebaseRef.child(Enemy).on("value", function(snapshot) {
                Enemyscore = snapshot.val();
                countUpEnemy(Enemyscore);
            });
            firebaseRef.child(username).on("value", function(snapshot) {
                userscore = snapshot.val();
                countUpMe(userscore);
            });
        });
        document.querySelector(".top-banner").style.display = "flex";
        firebaseRef = firebase.database().ref(GameKey);
        firebaseRef.child("GameStart").on("value", function(snapshot) {
            Gamestart = snapshot.val();
            if(Gamestart == "true"){
                document.getElementById("wrapperloader3").style.display = "none";
                document.querySelector(".demo").style.display = "inline";
                setTimeout(function(){
                    document.querySelector(".demo").style.display = "none";
                    //document.querySelector(".computer-container").style.transform = "scale(0.5) translate(0%,-70%)";
                    firebaseRef = firebase.database().ref(GameKey);
                    firebaseRef.child(username).set(0);
                    document.querySelector(".top-banner").style.display = "flex";
                    document.querySelector(".aside").style.display = "inline";
                    document.querySelector(".containerMonitor").style.display = "inline";
                    document.querySelector(".choose-username").style.display = "flex";
                    updateFlag();
                }, 5000);
            }
        });

      }
    }
  });  


}





